import React , { useState , useEffect } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import FloatingLabel from 'react-bootstrap/FloatingLabel';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Badge from 'react-bootstrap/Badge';
import Navbar from '../components/Navbar';
import Layout from '../components/layout';
import { navigate } from 'gatsby'
import countryCodes from '../utils/countryCodes.json';

import { register_user , check_mail , check_name , check_number } from '../api_handlers/account_api';

const signupLayout = {
    display: 'flex',
    flexFlow: 'column'
}
const formLayout = {
    margin: '3rem',
}

const pageLayout = {
	display:'flex',
	justifyContent:'stretch'
};

const introLayout = {
	width:'50%',
	display:'flex',
	flexFlow:'column',
	alignItems:'center',
	justifyContent:'center'
}

const introLines = {
	display:'flex',
	alignItems:'center',
	margin:'1rem 0',
	fontWeight:700,
	fontFamily:"'Poppins' , sans-serif"
}

const introLogo = {
	width:'7rem',
	marginLeft:'-1rem'
}

const lineBadge = {
	borderRadius:'50%',
	padding:'0 7px',
	color:'#fff',
	background:'#4f5df9'
}
const fillLine = {
	borderTop:'1px solid #999',
	width:'1.5rem',
	height:0,
}

const reset = {
	padding:0,
	margin:0
}
export default function Signup() {

const [name , setName] = useState('');
const [email , setEmail] = useState('');
const [number , setNumber] = useState('');
const [areaCode , setAreaCode] = useState('');
const [password , setPassword] = useState('');
const [currency , setCurrency] = useState('hkd')
const [house , setHouse] = useState('');
const [building , setBuilding] = useState('');
const [street , setStreet] = useState('');
const [city , setCity] = useState('');
const [postal, setPostal] = useState('');

const [page , setPage] = useState(0);
const [validated , setValidated] = useState();
const [error , setError] = useState(
{
	name:"",
	number:"",
	email:""
}
	)

const [ store_ , setStore_ ] = useState();
const [ csrfToken , setCsrfToken] = useState();

useEffect(()=>{
	if(store_ && typeof store_ === 'object' && Object.keys(store_).includes('seller_id'))
	localStorage.setItem('store_',JSON.stringify({...store_}));
}, [store_]);

useEffect(()=>{
	if(csrfToken !== 'undefined' && csrfToken){
	localStorage.setItem('csrf_token',csrfToken);
	navigate('/products');
}
}, [csrfToken]);

const stabilise_form = (e)=>{
	e.preventDefault();
	e.stopPropagation()
}

const change_page = async()=>{
	if(page === 0){
		
		let checkers = await Promise.all([check_mail(email),check_number(Number(areaCode.replace('+','')+number)), check_name(name)]);
		await console.log(checkers)
		if(checkers[0].msg !== 'success'){
			console.log(checkers[0]);
			setValidated(false)
			return setError({name:error.name,email:checkers[0].msg,number:error.number})
		}
	if(checkers[1].msg !== 'success'){
		console.log(checkers[1])
		setValidated(false)
			return setError({name:error.name,email:error.email,number:checkers[1].msg})
		}
	if(checkers[2].msg !== 'success'){
		console.log(checkers[2])
		setValidated(false)
			return setError({number:error.number,email:error.email,name:checkers[2].msg})
	}
		setValidated(true)
		return setPage(1)
	}
	
		let response = await register_user({
			shop_name:name , password, email , number:String(areaCode+number).replace('+','') , house, building , street , city , currency , postal 
		});
		console.log(response);
		if(response){
			setStore_({shop_name:name , number : response.number , email,  currency , seller_id:response.seller_id});
			setCsrfToken(response.token);
		}

}

useEffect(()=>{
	console.log(error)

},[error])

    return (
    <Layout>
    <div style={signupLayout}>
		<Navbar/>
		<div style={pageLayout}>
		<div style={introLayout}>
		<div>
		<img src="./static/primary_icon.png" style={introLogo} />
		<div style={{display:'flex' , justifyContent:'space-between' , flexFlow:'column'}}>
		<div style={introLines}>
		<span style={lineBadge}>1</span><div style={fillLine}></div><p style={reset}>Manage Orders from Whatsapp</p>
		</div>
		<div style={introLines}>
		<span style={lineBadge}>1</span><div style={fillLine}></div><p style={reset}>Lorem Ipsum dolor sit amet</p>
		</div>
		<div style={introLines}>
		<span style={lineBadge}>1</span><div style={fillLine}></div><p style={reset}>Lorem Ipsum dolor sit amet</p>
		</div>

		</div>
		</div>
		</div>
		<div style={formLayout}>
	{
	(page === 0)?
	<>
	<Form noValidate onSubmit={()=>stabilise_form} validated={validated} controlId="formGroup" className="mb-3">
	<h2>Sign Up</h2>
	
	<h4> Create your Merchant account with us</h4>
	
	<FloatingLabel
	controlId="email"
	label="Email"
	className="mb-3">
	
	
	<Form.Control type="email"  name="Email" onChange={(e)=>setEmail(e.target.value)} placeholder="Please enter your email"/>
	<Form.Control.Feedback type="invalid" name="Email">
	{error.email}
	</Form.Control.Feedback>
	</FloatingLabel>
	
	<Row>
	<Col>
	<Form.Select size="sm"  style={{height:"3.5rem"}} aria-label="Default select example">
      <option value="852" data-country="Hong Kong">+852</option> 
	  {
		countryCodes.map(data=>
			<option value={data.code} data-country={data.country} >+{data.code} {data.country.slice(0,7)}...</option>
		)
	  }
    </Form.Select>
    </Col>
    <Col xs={8}>
	<FloatingLabel
	controlId="number"
	label="Whatsapp No."
	className="mb-4">
	
	<Form.Control type="number"  name="number" onChange={(e)=>setNumber(e.target.value)}  placeholder="Please enter your Whatsapp Number"/>
	<Form.Control.Feedback type="invalid">
	{error.number}
	</Form.Control.Feedback>
	</FloatingLabel>
	
	</Col>
	</Row>
	<Form.Group as={Row}>
	<FloatingLabel
	controlId="shopname"
	label="Your Business or Shop's name"
	className="mb-3">
	
	<Form.Control type="text"  name="shopname" onChange={(e)=>setName(e.target.value)} placeholder="Your Business / Shop name"/>
	</FloatingLabel>
	<Form.Control.Feedback type="invalid">
	{error.name}
	</Form.Control.Feedback>
	</Form.Group>
	<FloatingLabel
	controlId="password"
	label="New Password"
	className="mb-3">
	
	<Form.Control type="password"  name="password" onChange={(e)=>setPassword(e.target.value)} placeholder="Please enter new password"/>
	</FloatingLabel>
	</Form>
	<div className="d-grid">
	<Button size="md" onClick={()=>change_page()}>Continue</Button>
	</div>
	</>
 :
<>
<Form.Group controlId="formGroup" className="mb-3">
	
	
	<h4>Provide us Details of your Business</h4>
	<FloatingLabel
	controlId="house"
	label="House / Shop No."
	className="mb-3">
	
	<Form.Control type="text"  name="house" onChange={(e)=>setHouse(e.target.value)} placeholder="Please enter Address Line of your shop"/>
	</FloatingLabel>
		<FloatingLabel
	controlId="building"
	label="Building name"
	className="mb-3">
	
	<Form.Control type="text"  name="building" onChange={(e)=>setBuilding(e.target.value)} placeholder="Please enter your Building's name"/>
	</FloatingLabel>
		<FloatingLabel
	controlId="street"
	label="Street Name"
	className="mb-3">
	
	<Form.Control type="text"  name="street" onChange={(e)=>setStreet(e.target.value)} placeholder="Please enter street name"/>
	</FloatingLabel>
<Row>	
<Col>
		<FloatingLabel
	controlId="city"
	label="City"
	className="mb-3">
	
	<Form.Control type="text"  name="city" onChange={(e)=>setCity(e.target.value)} placeholder="Please enter city"/>
	</FloatingLabel>
</Col>
<Col>
		<FloatingLabel
	controlId="postal"
	label="Postal Code"
	className="mb-3">
	
	<Form.Control type="number"  name="postal" onChange={(e)=>setPostal(e.target.value)} placeholder="Please enter your postal code"/>
	</FloatingLabel>
	</Col>
	</Row>
	</Form.Group>
	<div className="d-grid">
	<Button size="md" type="submit" onClick={()=>{change_page()}}>Submit</Button>
	</div>
</>
}
	
	<div style={{display:'flex',justifyContent:'center',alignItems:'center'}}>
	
      <Badge pill bg={`${page===0?'primary':'success'}`} style = {{transition:'1s ease-out'}} className="m-1">
			1
      </Badge>{''}
      <Badge pill bg={`${page===0?'secondary':'primary'}`} className='m-1'>
			2
      </Badge>{''}
	</div>
	</div>
 	</div>
	</div>
	</Layout>
    )
}